.Table {
    border-collapse: separate;
    border-spacing: 0px;
    padding: 0;
    width: 100%;
    padding-bottom: 43px;
}

.TableHeadingRow {
    width: 100%;
}

.TableHeadingRow th {
    text-align: left;
    padding: 20px 8px;
    font-size: 13px;
    background-color: #F3F5F7;
    position: sticky;
    top: 0;
    z-index: 6;
}

.TableHeadingRow th:first-child {
    padding-left: 20px;
    width: 80px;
}

.Container {
    width: 100%;
    height: 82%;
    height: 660px;
    border-radius: 15px;
    background-color: #fff;
    /* border: 1px solid #EEEEEE; */
    padding: 10px 0;
    position: relative;
    margin-bottom: 40px;
    box-shadow: 0 1px 3px hsla(0, 0%, 0%, 0.1);
}

.ScrollingContainer{
    overflow-y: auto;
    /* z-index: 2; */
    height: 90%;
    margin-bottom: 100px;
    position: relative;
}

.Tbody_loading:after {
    position: absolute;
    top: 0;
    left: 0;
    right: 0; 
    bottom: 0;
    background-color: rgba(109, 109, 109, 0.062);
    background-image: url(data:image/gif;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 50px 50px;
    content: "";
}

.Header {
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 20px;
}

.Header h1{
    font-size: 22px;
}

.Search {
    display: flex;
}

.SearchContainer {
    display: flex;
    align-items: center;
    margin-right: 15px;
    position: relative;
    transition: all 0.2s linear;
}

.SearchBar {
    height: 30px;
    padding: 2px 12px;
    padding-left: 24px;
    width: 20vw;
    background-color: #e6ecf207;
    border: 1.8px solid #dbdbe0;
    border-right: 0;
    border-left: 0;
    color: black;
    font-size: 15px;
    outline: none;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
    transition: all 0.2s linear;
}

.SearchBar:focus{
    border: 1.8px solid #8bb8e8;
    border-right: 0;
    border-left: 0;
	background-color: #e6ecf223;
	transition: all 0.1s linear;
}

.SearchButton {
    height: 38px;
    width: 44px;
    border-radius: 8px;
    padding: 2px 0;
    background-color: #fff;
    border: 1px solid #dbdbe0;
    outline: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: -5px;
    transition: all 0.2s linear;
}

::-webkit-scrollbar {
    width: 0;
}

.AddButton{
    height: 36px;
    width: 128px;
    box-shadow: 0 1px 3px hsla(0, 0%, 0%, 0.2);
    background-color: #8bb8e8;
    color: #fff;
    border-radius: 8px;
    border: 0;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 22px;
    margin-right: 15px;
    cursor: pointer;
    font-weight: 600;
}

.FilterDropdown{
    display: flex;
    border-radius: 8px;
    border: 1px solid #dbdbe0;
    outline: none;
    width: 120px;
    padding: 8px 0;
    margin-right: -10px;
    z-index: 1;
    padding-left: 8px;
}

.PagenationController {
    position: absolute;
    bottom: 0;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    box-sizing: border-box;
    padding-right: 30px;
    background-color: #fff;
    z-index: 3;
    width: 100%;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    border-top: 1px solid #EEEEEE;
}

.PagenationController select{
    font-size: 16px;
    margin-right: 12px;
    border: 0;
    border-bottom: 1px solid #000000;
    margin-bottom: -2px;
    outline: none;
    cursor: pointer;
}

.RowsPerPage{
    margin-right: 6px;
}

.Ranges{
    margin-right: 8px;
}

.ChevronActions {
    display: flex;
    align-items: center;
}

.LeftButton, .RightButton, .LeftButton_disabled, .RightButton_disabled {
    border-radius: 50%;
    height: 48px;
    width: 48px;
    background-color: transparent;
    border: 0;
    outline: none;
    cursor: pointer;
    transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-size: 1.5rem;
    text-align: center;
    flex: 0 0 auto;
    color: rgba(0, 0, 0, 0.54);
    padding: 12px;
}

.LeftButton:hover, .RightButton:hover {
    background-color: rgba(0, 0, 0, 0.04);
    transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.LeftButton_disabled, .RightButton_disabled {
    cursor: default;
}

